import { Collapse } from "@material-ui/core";
import { useState } from "react";
import { ButtonBar } from "../CommonCpns";
import { hasDup } from "../MyArtifacts/AdvFilter";
import { initArtStatFilter } from "../../helpers/misc";
import useArtStatsFilter from "../../helpers/hooks/useArtStatsFilter";
import useArtSetFilter from "../../helpers/hooks/useArtSetFilter";

export default function ArtFilter(props) {
  const { filter, close } = props;
  const [err, setErr] = useState(false);
  const [stats, statsFilter, setStats] = useArtStatsFilter(filter.stats, err);
  const [sets, setFilter] = useArtSetFilter(
    props.arts,
    filter.sets,
    props.artType
  );
  const resetable = stats.main !== "All" || stats.subs.some((s) => s !== "All");

  return (
    <div className="filter_area">
      <Collapse in={props.filterOn}>
        <div className="p-3 full-w flex justify-center">
          <div className="flex custom-sb">
            <div>
              <ButtonBar
                className="mb-2"
                texts={["Reset Stats"]}
                availables={[resetable]}
                handlers={[
                  () => {
                    if (resetable) setStats(initArtStatFilter());
                  }
                ]}
              />
              {statsFilter}
              <ButtonBar
                className="mt-3"
                texts={["Cancel", "Confirm"]}
                handlers={[
                  close,
                  () => {
                    if (hasDup(stats)) {
                      setErr(true);
                      return;
                    }
                    filter.setStats(stats);
                    filter.setSets(sets);
                    close();
                  }
                ]}
              />
            </div>
            {setFilter}
          </div>
        </div>
      </Collapse>
    </div>
  );
}
