import styled from "styled-components";
import { useState, useEffect } from "react";

const StyledDiv = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  height: 0;
  transition: 300ms ease-in-out;
  &.on {
    height: 100%;
  }
`;

export default function HiddenSpace(props) {
  const { on, className } = props;
  const [open, setOpen] = useState(false);
  const [showing, setShowing] = useState(false);

  useEffect(() => {
    if (on) {
      setOpen(true);
      setShowing(true);
    } else {
      setOpen(false);
      setTimeout(() => setShowing(false), 300);
    }
  }, [on]);

  return (
    <StyledDiv
      className={(className ? className + " " : "") + (open ? "on" : "")}
    >
      {showing && props.children}
    </StyledDiv>
  );
}
